.searchBox {
  width: 40%;
  margin: 0 auto;
  padding: 10px 5px;
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.searchBar {
  width: 80%;
  padding: 15px;
  -webkit-appearance: none;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  color: #313131;
  font-size: 20px;
  color: white;
}

.searchBar::placeholder {
  color: white;
  opacity: 0.8;
}

.mainBtn {
  margin-right: 10px;
  padding: 6px 15px;
  font-size: 20px;
  border: none;
  border-radius: 2px;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  color: white;
}

@media only screen and (max-width: 360px) {
  .searchBox {
    width: 100%;
  }
}
