.weatherResults {
  width: 40%;
  margin: 30px auto;
  position: relative;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 20px;
}

.locationBox {
  margin: 50px auto;
  font-size: 30px;
}

.weatherBox {
  display: grid;
  grid-template-columns: 2fr 3fr;
  column-gap: 10px;
}

.weatherBox img {
  display: flex;
  justify-self: center;
  align-self: center;
}

.weatherBoxInfo .temp {
  color: #fff;
  font-size: 100px;
  font-weight: 900;
}

.weatherBoxInfo .realFeel {
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  margin-top: -15px;
  margin-bottom: 10px;
}

.weatherBoxInfo .weather {
  color: #fff;
  font-size: 40px;
  font-weight: 700;
  text-transform: capitalize;
}

@media only screen and (max-width: 360px) {
  .weatherResults {
    width: 100%;
  }

  .locationBox {
    margin: 20px auto;
    font-size: 28px;
    text-align: center;
    color: white;
    text-transform: uppercase;
    font-weight: 700;
  }

  .weatherBox img {
    align-self: flex-start;
  }

  .weatherBoxInfo .temp {
    font-size: 40px;
    font-weight: 700;
  }

  .weatherBoxInfo .realFeel {
    font-size: 18px;
    font-weight: 500;
    margin-top: 0px;
    margin-bottom: 0px;
  }

  .weatherBoxInfo .weather {
    font-size: 26px;
    font-weight: 500;
  }
}

.followCity {
  width: 100%;
  margin-top: 25px;
  padding: 10px;
  outline: none;
  border: none;
  background-color: #222;
  font-size: 22px;
  color: white;
}

.returnBtn {
  background-color: rgba(255, 255, 255, 0.5);
  color: white;
  width: 100%;
  padding: 10px;
  outline: none;
  border: none;
  text-transform: uppercase;
  font-size: 20px;
}